// /components/PostList.tsx
import { useState } from "react";
import { Post } from "../lib/posts";
import PostCard from "./PostCard";
import styles from "../styles/PostList.module.css";
import { Diary } from "../lib/diary";

interface PostListProps {
  // posts: Post[];
  posts: Diary[];
  pageSize?: number; // ÊØèÈ°µÊòæÁ§∫Êï∞Èáè
}

const isMobile = typeof window !== "undefined" && window.innerWidth < 768;

export default function PostList({ posts, pageSize = 9 }: PostListProps) {
  const [viewMode, setViewMode] = useState<"card" | "list">("card");
  const [currentPage, setCurrentPage] = useState(1);

  const totalPages = Math.ceil(posts.length / pageSize);
  const startIndex = (currentPage - 1) * pageSize;
  const paginatedPosts = posts.slice(startIndex, startIndex + pageSize);

  return (
    <div>
      {/* ÂàáÊç¢ÂõæÊ†áÊåâÈíÆ */}
      <div className={styles.viewSwitch}>
        <img
          src="/icon/view_gallery.svg"
          alt="Âç°ÁâáËßÜÂõæ"
          className={styles.iconButton}
          onClick={() => setViewMode("card")}
        />
        <img
          src="/icon/view_list.svg"
          alt="ÂàóË°®ËßÜÂõæ"
          className={styles.iconButton}
          onClick={() => setViewMode("list")}
        />
      </div>

      {/* posts ÂàóË°® */}
      <div
        style={{
          display: "grid",
          gridTemplateColumns:
            viewMode === "card"
              ? isMobile
                ? "repeat(2, 1fr)"
                : "repeat(3, 1fr)"
              : "1fr",
          gap: "1rem",
        }}
      >
        {paginatedPosts.map((post) => (
          <PostCard key={post.slug} post={post} view={viewMode} />
        ))}
      </div>

      {/* ÁøªÈ°µÊåâÈíÆ */}
      {totalPages > 1 && (
        <ul className={styles.pageNumbers}>
          {/* ‰∏ä‰∏ÄÈ°µ */}
          <li>
            <button
              className={`${styles.pageNumber} ${styles.prev}`}
              onClick={() => setCurrentPage((p) => Math.max(p - 1, 1))}
              disabled={currentPage === 1}
            >
              ¬´
            </button>
          </li>

          {/* È°µÁ†Å */}
          {Array.from({ length: totalPages }, (_, i) => i + 1).map((page) => {
            // ÊòæÁ§∫Á¨¨‰∏ÄÈ°µ„ÄÅÊúÄÂêé‰∏ÄÈ°µ„ÄÅÂΩìÂâçÈ°µÂâçÂêéÂêÑ‰∏ÄÈ°µ
            if (
              page === 1 ||
              page === totalPages ||
              (page >= currentPage - 1 && page <= currentPage + 1)
            ) {
              return (
                <li key={page}>
                  {page === currentPage ? (
                    <span className={`${styles.pageNumber} ${styles.current}`}>
                      {page}
                    </span>
                  ) : (
                    <button
                      className={styles.pageNumber}
                      onClick={() => setCurrentPage(page)}
                    >
                      {page}
                    </button>
                  )}
                </li>
              );
            }

            // ÂâçÂêéÁúÅÁï•Âè∑
            if (page === currentPage - 2 || page === currentPage + 2) {
              return (
                <li key={page}>
                  <span className={`${styles.pageNumber} ${styles.dots}`}>
                    ‚Ä¶
                  </span>
                </li>
              );
            }

            return null;
          })}

          {/* ‰∏ã‰∏ÄÈ°µ */}
          <li>
            <button
              className={`${styles.pageNumber} ${styles.next}`}
              onClick={() => setCurrentPage((p) => Math.min(p + 1, totalPages))}
              disabled={currentPage === totalPages}
            >
              ¬ª
            </button>
          </li>
        </ul>
      )}
    </div>
  );
}
